import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { get } from 'mobx';
import { Tooltip } from 'antd';
import { FixedSizeGrid as Grid } from 'react-window';
import { withTranslation } from 'react-i18next';
import AutoSizer from "react-virtualized-auto-sizer";
import Loading from '../../common/Widget/Loading';
import Nodata from '../../common/Widget/Nodata';
// import Thumbnail from '../../common/Thumbnail/index';
import { getFile } from '../../util/util';
import Goblack from '../../common/Goblack';

@withTranslation() @inject('login', 'albumAdd') @observer
class Albumdetails extends Component {
  constructor(props) {
    super(props);
    this.imgRef = React.createRef();
  }
  componentDidMount() {
    this.props.albumAdd.getData(this.getWH, this.getH);
    window.addEventListener('resize', this.getWH);
  }
  //计算宽高
  getWH = (flag) => {
    try {
      let wi, hi, w = 135, h = 120;
      this.w = this.imgRef.current.offsetWidth;
      this.h = this.imgRef.current.offsetHeight;
      let Width = this.imgRef.current.offsetWidth - 18;
      let row = Math.floor(Width / w);
      let space = Width % w;
      wi = space / row + w;
      hi = wi / w * h;
      this.childW = wi;
      this.childH = hi;
      this.columnCount = Math.floor(Width / w);
      console.log(this.h)
      if (!flag) {
        this.getH();
      }
      console.log('this.columnCount>>>>>>>', this.columnCount)
      return this.columnCount;
    } catch (error) {
      console.log(error);
      // this.getWH();
    }
  }

  getH = () => {
    const { detailList } = this.props.albumAdd;
    this.rowCount = detailList.length / this.columnCount;
    console.log('this.rowCount', this.rowCount)
  }

  componentWillMount() {
    window.removeEventListener('resize', this.getWH);
  }
  render() {
    const { t } = this.props;
    const {
      detailList,
      partlist,
      name,
      goback,
      checkclick,
      SelectImgs,
      checkAll,
      invert } = this.props.albumAdd;
    const { OS } = this.props.login;
    const ps = OS === 'tab' ? { visible: false } : {};
    let fileNum = 0;
    SelectImgs.forEach(item => {
      if (!item.true) {
        fileNum += 1;
      }
    });
    return (
      <div className='detail-inner'>
        <span className='check-all'>
          <i
            className={`iconfont check-photoalbum treeNodeUnselectable ${detailList.length === SelectImgs.length && detailList.length > 0 ? 'icon-xuanzhong1' : 'icon-weixuanzhong'}`}
            onClick={checkAll}></i>
          {fileNum > 0 ? `${t('Selected')} ${fileNum} ${t('file')}` : t('Allelection')}
          {fileNum > 0 && <span onClick={invert} className='invert-Selection'>{t('Reverseselection')}</span>}
        </span>

        <div className='top'>
          <Goblack go={goback} name={name} />
        </div>
        <div className='list-box' ref={this.imgRef}>
          {partlist ? detailList.length > 0 ? (
            <AutoSizer>
              {({ height, width }) => (
                <Grid
                  className="Grid"
                  itemData={detailList}
                  rowCount={this.rowCount}
                  columnCount={this.columnCount}
                  columnWidth={this.childW}
                  height={height}
                  rowHeight={this.childH}
                  width={width}
                >
                  {({ columnIndex, data, rowIndex, style }) => {
                    const item = data[rowIndex * this.columnCount + columnIndex]
                    try {
                      if (get(item, 'true')) {
                        return <div style={{ visibility: 'hidden' }}></div>
                      } else {
                        return (
                          <Tooltip
                            key={item.id}
                            overlayClassName='tooltip'
                            title={item.cpt}
                            {...ps}>
                            <div
                              style={style}
                              className={`thumbnail2 ${get(item, 'check') ? 'checkon' : ''}`}
                              onClick={() => { checkclick(item.id) }}
                            >
                              <img
                                className='img_box'
                                src={getFile.file_s(item.id)}
                                alt='' />
                                <span
                                style={OS === 'tab' ? { visibility: 'inherit' } : {}}
                                className='icon-check-s treeNodeUnselectable'>
                                  <i className='iconfont' style={get(item, 'check') ? {} : { visibility: "hidden" }}>&#xe62d;</i>
                                </span>
                              {item.ext === 'gif' && <i className='iconfont GIF-IMG'>&#xe670;</i>}
                              <span className='sign'>{rowIndex * this.columnCount + columnIndex + 1}</span>
                              {item.cpt && <i className='iconfont icon'>&#xe604;</i>}
                              {/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(item.ext) && <i className='iconfont v_icon'>&#xe6ae;</i>}
                            </div>
                          </Tooltip>
                        );
                      }
                    } catch (error) {
                      return <div style={{ visibility: 'hidden' }}></div>
                    }
                  }}
                </Grid>
              )}
            </AutoSizer>
          ) : <Nodata /> : <Loading />}

        </div>
      </div>
    );
  }
}

export default Albumdetails;