import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { withTranslation } from 'react-i18next';
import dayjs from 'dayjs';
import { getFile } from '../../util/util';
import Loading from '../../common/Widget/Loading';
import Nodata from '../../common/Widget/Nodata';
import LazyImage from '../../common/Lazy-image';
import imgload from '../../images/imgload.gif';
import imgload2 from '../../images/imgerror.png';

@withTranslation() @inject('login', 'photoalbum', 'albumAdd') @observer
class Albumlist extends Component {
  componentDidMount() {
    this.props.albumAdd.getAlbum();
  }

  render() {
    const { t } = this.props;
    const { list,getDetail } = this.props.albumAdd;
    return (
      <div className='list-body clearfix'>
        {list ? (
          list.length > 0 ? list.map(item => {
            return (
              <div
                key={item.id}
                className='albumslist'
                onClick={(e) => { getDetail(item.id, item.name) }}>
                <div className='top-img' style={item.profile ? { backgroundImage: `url(${imgload})` } : { backgroundImage: `url(${imgload2})`,backgroundSize:'100% 100%' }}>
                  {item.profile && <LazyImage
                    src={getFile.file_s(item.profile.id)}
                    alt={item.profile.id}
                    w='226px' />}
                </div>
                <div className='bottom-information'>
                  <div className='name-time'>
                    <p className='album-name ellipsis'>{item.name}</p>
                    <p className='update-time'>
                      <span>{t('LastUpdate')}{item.updated ? dayjs(item.updated).format(t("L")) : dayjs(item.created).format(t("L"))}</span>
                    </p>
                    <span className='photo-num'>{t('Photo')}:{item.filecount}</span>
                  </div>
                </div>
              </div >
            )
          }) : <Nodata />
        ) : <Loading />}
      </div>
    );
  }
}

export default Albumlist;

