import React from 'react';
import { inject, observer } from 'mobx-react';
import { get } from 'mobx';
import { getFile } from '../../util/util';
import error from '../../images/imgerror.png'
import './style.scss'
import { Tooltip } from 'antd';

@inject('playdetail', "login") @observer
class Thumbnail extends React.PureComponent {
  constructor(props) {
    super(props);
    this.imgRef = React.createRef();
  }

  componentDidMount() {
    const { columnIndex, data, rowIndex } = this.props;
    const item = data[rowIndex * this.props.playdetail.columnCount + columnIndex];
    if (item) {
      let url = getFile.file_s(item.id);
      if (this.imgRef.current) this.imgRef.current.style.background = `#d3d3d3 url(${url}) center center no-repeat`;
    }
  }

  UNSAFE_componentWillReceiveProps(next) {
    const { columnIndex, data, rowIndex } = next;
    const item = data[rowIndex * this.props.playdetail.columnCount + columnIndex];
    if (item) {
      let url = getFile.file_s(item.id);
      if (this.imgRef.current) this.imgRef.current.style.background = `#d3d3d3 url(${url}) center center no-repeat`;
    }
  }


  errorImg = (e) => {
    const el = e.target;
    el.src = error;
  }


  render() {
    const { columnIndex, data, rowIndex, style } = this.props;
    const {
      columnCount,
      checkclick,
      b_img_show
    } = this.props.playdetail;
    const { OS } = this.props.login;
    const ps = OS === 'tab' ? { visible: false } : {};
    const item = data[rowIndex * columnCount + columnIndex];
    try {
      if (get(item, 'true')) {
        return <div style={style}></div>
      } else {
        return (
          <Tooltip title={item.cpt} overlayClassName='tooltip' {...ps}>
            <div
              className={`thumbnail ${get(item, 'check') ? 'selected' : ''}`}
              style={style}
              ref={this.imgRef}
              onClick={() => { b_img_show(rowIndex * columnCount + columnIndex, item.id) }}>
              <span
                style={OS === 'tab' ? { visibility: 'inherit' } : {}}
                className='icon-check-s treeNodeUnselectable'
                onClick={(e) => { checkclick(e, item) }} >
                <i className='iconfont' style={get(item, 'check') ? {} : { visibility: "hidden" }}>&#xe62d;</i>
              </span>
              <span className='sign'>{rowIndex * columnCount + columnIndex + 1}</span>
              {item.ext === 'gif' && <i className='iconfont GIF-IMG'>&#xe670;</i>}
              {item.cpt && <i className='iconfont icon'>&#xe604;</i>}
              {/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(item.ext) && <i className='iconfont v_icon'>&#xe6ae;</i>}
            </div>
          </Tooltip>
        );
      }
    } catch (error) {
      return <div style={style}></div>
    }
  }
}

export default Thumbnail;
