import React, { Component } from 'react';
import { Modal, Icon } from 'antd';
import { inject, observer } from 'mobx-react';
import { withTranslation } from 'react-i18next';
import './style.scss';
import { get } from 'mobx';
import { strRep, getFile } from '../../util/util';
import Nodata from '../../common/Widget/Nodata';
import Loading from '../../common/Widget/Loading';

@withTranslation() @inject('login') @observer
class Chooseframe extends Component {

  render() {
    const {
      visible,
      title,
      framelist,
      onOk,
      onCancel,
      id,
      click,
      checkAllfn,
      framelists } = this.props;
    const { OS } = this.props.login;
    const { t } = this.props;
    return (
      <Modal
        title={title}
        visible={visible}
        getContainer={false}
        destroyOnClose={true}
        width={'70vw'}
        okText={t('Determine')}
        cancelText={t('Cancel')}
        onOk={onOk}
        onCancel={onCancel}>
        {
          framelist && <div className='framecheckAll'>
              <i
                className={`iconfont check-frame treeNodeUnselectable ${framelist.length === framelists.length && framelist.length > 0 ? 'icon-xuanzhong1' : 'icon-weixuanzhong'}`}
                onClick={checkAllfn}></i>
            <span className='checkAll-text'>{t('Allelection')}</span>
          </div>
        }
        <div className='frame_list'>
          {
            framelist ? framelist.length > 0 ? framelist.map(item => {
              return (
                <div
                  key={item.node}
                  className={`choose-framer-list ${get(item, 'check') ? 'choose-framer-list-check' : null}`}
                  onClick={() => { click(item) }}>
                  <span
                    style={OS === 'tab' ? { visibility: 'inherit' } : {}}
                    className={`choose-framerlist-check ${item.playlist_id === id ? 'defaultcheck' : ''}`}>
                    <Icon type="check" />
                  </span>
                  <div className='choose-framer-cover'>
                    <div className='cover-img'>
                      <span
                        className='back-img'
                        style={item.profile ? { background: `url("${getFile.file_s(item.profile.id)}") no-repeat`, backgroundSize: '100% 100%' } : {}}></span>
                    </div>
                  </div>
                  <div className='choose-framer-name-series'>
                    <div className='name clearfix'>
                      <p className='frame-name ellipsis'>{item.name}</p>
                      <div className='admin_default'>
                        <span
                          className='admin'
                          style={item.playlist_id === id ? { visibility: 'initial' } : { visibility: 'hidden' }}>
                          {t('default')}
                        </span>
                      </div>
                    </div>
                    <p className='series'>SN : {strRep(item.serial)} </p>
                  </div>
                </div>
              )
            }) : <Nodata /> : <Loading />
          }
        </div>
      </Modal>
    );
  }
}

export default Chooseframe;