import React from 'react';
import { Menu, Dropdown, Button, Icon, message } from 'antd';
import { withTranslation } from 'react-i18next';
import { inject, observer } from 'mobx-react';
import dayjs from 'dayjs';
import { FixedSizeGrid as Grid } from 'react-window';
import AutoSizer from "react-virtualized-auto-sizer";
// import { Sortable, MultiDrag } from '../../util/sortable.esm';
import { observable } from 'mobx';
import './style.scss';
import Thumbnail from '../../common/Thumbnail';
import Upinput from '../../common/Upinput';
import BigimgCpt from '../../common/BigimgCpt.js';
import Loading from '../../common/Widget/Loading';
import Nodata from '../../common/Widget/Nodata';
import Cloudalbum from './Cloudalbum';
import Chooseframe from './Chooseframe';
import Information from '../../common/Information';
import { wsget } from '../../ws';
import event from '../../util/event';
import Goblack from '../../common/Goblack';
import Toframes from './Toframes';
import i18next from 'i18next';

// Sortable.mount(new MultiDrag());


@withTranslation() @inject('playlist', 'uploadFiles', 'login', 'rahmen', 'playdetail') @observer
class Playlistdetails extends React.Component {
  constructor(props) {
    super(props);
    this.renameRef = React.createRef();
    this.imgRef = React.createRef();
    this.frameRef = React.createRef();
  }
  @observable num = 100; //一页加载个数
  @observable sortableInstance = null;
  @observable init = false; //初始化
  detail_list = { list: [] };//用来展示列表详情
  detail_list_2 = { list: [] };//操作的列表详情
  checkAll = false;//是否全选
  @observable removeFileids = [];//选中的图片的列表
  @observable bImg_visible = false;//大图modul
  @observable defaultValue = '';//图片描述
  @observable imgfile = {};//大图文件
  @observable imgidx = null;//切换图片的标识
  @observable set_visible = false;//查看绑定相框显隐开关
  @observable playlistFrames_load = false;//查看绑定相框loading
  @observable frameList = [];//绑定相框de列表
  @observable frameListNum = 0;//绑定相框个数
  @observable info_visible = false;//列表信息显隐
  @observable infoObj = {};//列表信息数据
  @observable dragIndex = null;//列表信息数据
  checkidxArr = [];
  @observable name = '';
  @observable id = null;
  @observable list = []; //渲染的数据
  @observable page = 0; //第一条数据
  @observable hasMoreItems = true;
  @observable number = this.num;
  @observable frames_visible = false;  //选择相框显隐
  @observable albumvisible = false;  //云相册添加图片显隐
  @observable filenum = 0;  //文件数量
  @observable created = null;  //创建时间
  @observable updated = null;  //更新时间
  @observable h = 0;  //更新时间
  @observable w = 0;  //更新时间
  flag = true; //拖拽保存列表的标记

  // @observable initialData = [];
  @observable initialData = [];
  @observable key = 1;


  async componentDidMount() {
    try {
      const { id } = this.props.match.params;
      const { name, created } = this.props.playlist.urlData;
      if (!name) {
        const playArr = await wsget('playlist_list', null);
        const pos = playArr.findIndex(item => item.id === Number(id));
        if (pos === -1) {
          this.props.history.push(`/playlist`);
        }
        this.name = playArr[pos].name;
        this.created = playArr[pos].created;
      } else {
        this.name = name;
        this.created = created;
      }
      this.id = Number(id);
      this.get();
      this.props.playdetail.getData(this.id, this.getWH, this.getH, this.w);

      window.addEventListener('resize', this.myresize);
      // window.addEventListener('resize', this.myresize2);

      this.wsinit = event.on('wsinit', (res) => {
        if (res === 1) {
          if (!this.props.playdetail.init) {
            this.props.playdetail.getData(this.id, this.getWH, this.getH, this.w);
          }
        }
      })
    } catch (error) {
      this.props.history.push("/playlist");
    }
  }

  componentWillUnmount() {
    event.rmName('framesNum');
    event.rmName('rmCheck');
    window.removeEventListener('resize', this.myresize);
    window.removeEventListener('resize', this.myresize2);
    event.rm(this.wsinit);
    this.props.playdetail.clearData();
  }

  myresize = () => {
    this.get();
    this.props.playdetail.resize(this.getWH);
    this.props.playdetail.resize2(this.w, this.props.playdetail.frameList);
  }

  get = () => {
    if (this.key === 1) {
      this.w = this.imgRef.current.offsetWidth;
      this.h = this.imgRef.current.offsetHeight;
    } else {
      this.w = this.frameRef.current.offsetWidth;
      this.h = this.frameRef.current.offsetHeight;
    }
  }

  //计算宽高
  getWH = (flag) => {
    try {
      let wi, hi, w = 165, h = 150;
      let Width = this.w - 18;
      let row = Math.floor(Width / w);
      let space = Width % w;
      wi = space / row + w;
      hi = wi / w * h;
      this.childW = wi;
      this.childH = hi;
      this.columnCount = Math.floor(Width / w);
      // console.log(this.h)
      if (!flag) {
        this.getH();
      }
      // console.log('this.columnCount>>>>>>>', this.columnCount)
      return this.columnCount;
    } catch (error) {
      console.log(error);
      // this.getWH();
      this.props.history.push("/playlist");
    }
  }

  getH = () => {
    const { initialData } = this.props.playdetail;
    this.rowCount = initialData.length / this.columnCount;
    // console.log('this.rowCount', this.rowCount)
  }

  //详情显示
  info_show = () => {
    const infoObj = [
      { id: 1, icon: 'file-image', txt: 'document', data: this.props.playdetail.filenum },
      { id: 2, icon: 'file-image', txt: 'BindingFrame', data: this.props.playdetail.frameListNum },
      { id: 3, icon: 'clock-circle', txt: 'CreationTime', data: dayjs(this.created).format(this.props.t('LL')) },
      { id: 4, icon: 'clock-circle', txt: 'LastUpdate', data: dayjs(this.props.playdetail.updated).format(this.props.t('LL')) },
    ]
    this.info_visible = true;
    this.infoObj = infoObj;
  }

  //上传
  uploading = (e, reset) => {
    const { id } = this;
    this.props.uploadFiles.upFile(e, id, reset, this.props.playdetail.updated)
  }

  //返回
  goblack = () => {
    this.props.history.push(`/playlist`);
  }

  //选择相框显示
  chooseframe = () => {
    this.frames_visible = true;
    this.props.playdetail.getframes(this.id);
  }
  //播放到相框
  frameonPlay = (frames) => {
    if (!this.props.playdetail.initialData.length > 0) {
      message.warning(i18next.t('Listempty'));
      return;
    }
    wsget('playlist_play_in_frame', { id: this.id, frames }, 1)
  }
  handleOk = e => {
    this.frames_visible = false;
    this.props.playdetail.handleOk();
  };
  handleCancel = e => {
    this.frames_visible = false;
    this.props.playdetail.framesno()
  }
  //选择相框end

  //云相册添加
  cloudAlbumUpload = () => {
    this.albumvisible = true;
  }
  albumCancel = () => {
    this.albumvisible = false;
  }

  onchange = (key) => {
    this.key = key;
    if (key === 2) {
      this.props.playdetail.getFrames(this.id, this.w);
    }
  }

  myresize2 = () => {
    this.props.playdetail.resize2(this.frameRef.current.offsetWidth, this.props.playdetail.frameList);
  }

  render() {
    const { t } = this.props;
    const {
      frames_visible,
      info_visible,
      infoObj,
      id,
      name,
      albumvisible,
      created } = this;
    const {
      init,
      initialData,
      oncheckAll,
      removeFileids,
      remove_img,
      filenum,
      bImg_visible,
      closeDialog,
      imgfile,
      onPiece,
      nextPage,
      addCpt,
      frames,
      checkFrames,
      frameList,
      frameListNum,
      updated,
      albumOk,
      invert,
      moveTop,
      moveLast,
      checkAllfn,
      framelists,
      num } = this.props.playdetail;

    const menu = (
      <Menu>
        <Menu.Item key="0">
          <span className='upphoto-btn-icon'></span>
          <Upinput className='upphoto-btn' onupload={this.uploading} />
        </Menu.Item>
        <Menu.Item key="1">
          <span className='cloudAlbumUpload' onClick={this.cloudAlbumUpload}>
            <span className='cloudAlbumUpload-icon'></span>
            <span>{t('albumAdd')}</span>
          </span>
        </Menu.Item>
      </Menu>
    );



    return (
      <div className='playlistdetails'>
        <div className='playlist-header'>
          <Goblack go={this.goblack} name={name} />
          <div className='p-details-information'>
            {this.key === 2 ? (<div className='choose-frame'>
              <span onClick={this.chooseframe}>{t('Chooseframe')}</span>
            </div>) : (
                <Dropdown overlay={menu} trigger={['click']} size='large'>
                  <Button type="primary">
                    {t('Uploadphotos')} <Icon style={{ fontSize: '14px' }} type="down" />
                  </Button>
                </Dropdown>
              )}
            <div className={`file-num ${this.key === 1 ? 'befort' : ''}`} onClick={() => { this.onchange(1) }}>
              <div className='p-d-i-icon'>
                <i className='iconfont icon'>&#xe785;</i>
              </div>
              <div className='num-text'>
                <p className='text'>{t('Photo')}</p>
                <p className='num'>{filenum}</p>
              </div>
            </div>
            <div className={`frame-num ${this.key === 2 ? 'befort' : ''}`} onClick={() => { this.onchange(2) }}>
              <div className='p-d-i-icon'>
                <i className='iconfont icon'>&#xe607;</i>
              </div>
              <div className='num-text'>
                <p className='text'>{t('photoframe')}</p>
                <p className='num'>{frameListNum}</p>
              </div>
            </div>
            <div className="Time">
              <div className='creation-time'>
                <div className='p-d-i-icon'><span className='creation-time-icon'></span></div>
                <div className='num-text'>
                  <p className='text'>{t('CreationTime')}</p>
                  <p className='num'>{created && dayjs(created).format(t('LL'))}</p>
                </div>
              </div>
              <div className='last-update'>
                <div className='p-d-i-icon'><span className='last-update-icon'></span></div>
                <div className='num-text'>
                  <p className='text'>{t('LastUpdate')}</p>
                  <p className='num'>{updated && dayjs(updated ? updated : created).format(t('LL'))}</p>
                </div>
              </div>
            </div>
            <div className='playlist-set'>
              <div className='playlist-msg-icon' onClick={this.info_show}>
                <Icon type="exclamation" />
              </div>
            </div>
          </div>
        </div>

        <div className='playlistdetails-boby'>
          <div className='playlistdetails-main'>
            <div className='playlistdetails-main-left' style={this.key === 1 ? {} : { display: "none" }}>
              <div className='playlist-operation'>
                {init && <div className='checkAll'>
                  <i className={`iconfont treeNodeUnselectable ${initialData.length === removeFileids.length && initialData.length > 0 ? 'icon-xuanzhong1' : 'icon-weixuanzhong'}`}
                    onClick={oncheckAll}></i>
                  <span className='checkAll-text'>{num > 0 ? `${t('Selected')} ${num} ${t('file')}` : t('Allelection')}</span>
                  {removeFileids.length > 0 && <span onClick={invert} className='invert-Selection'>{t('Reverseselection')}</span>}
                </div>}
                {removeFileids.length > 0 && <div className='playlist-delete'>
                  <span onClick={() => { moveTop(id) }}>{t('Movefront')}</span>
                </div>}
                {removeFileids.length > 0 && <div className='playlist-delete'>
                  <span onClick={() => { moveLast(id) }}>{t('Movelast')}</span>
                </div>}
                <div className='playlist-delete' style={removeFileids.length > 0 ? {} : { visibility: 'hidden' }}>
                  <span onClick={() => { remove_img(id) }}><Icon type="delete" />{t('delete')}</span>
                </div>
              </div>
              <div ref={this.imgRef} className='imglist-inner'>
                {init ? initialData.length > 0 ? (
                  <AutoSizer>
                    {({ height, width }) => (
                      <Grid
                        className="Grid"
                        itemData={initialData}
                        rowCount={this.rowCount}
                        columnCount={this.columnCount}
                        columnWidth={this.childW}
                        height={height}
                        rowHeight={this.childH}
                        width={width}
                      >
                        {Thumbnail}
                      </Grid>
                    )}
                  </AutoSizer>
                ) : <Nodata /> : <Loading />}
              </div>
            </div>

            <div ref={this.frameRef} className='toframes' style={this.key === 2 ? {} : { display: "none" }}>
              <Toframes
                framelist={frameList}
                id={id}
                initload={init}
                framePlay={this.frameonPlay} />
            </div>
          </div>
        </div>

        {
          bImg_visible && <BigimgCpt
            closeDialog={closeDialog}
            imgfile={imgfile}
            onPiece={onPiece}
            nextPage={nextPage}
            blur={addCpt} />
        }

        <Chooseframe
          title={t('Chooseframe')}
          visible={frames_visible}
          framelist={frames}
          id={id}
          click={checkFrames}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          checkAllfn={checkAllfn}
          framelists={framelists}
        />

        <Cloudalbum
          visible={albumvisible}
          onOk={albumOk}
          handleCancel={this.albumCancel} />

        {
          info_visible && <Information
            data={infoObj}
            handlecancel={() => { this.info_visible = false }} />
        }
      </div >
    );
  }
};

export default Playlistdetails;