import React from 'react';
import { Modal, Button } from 'antd';
import { inject, observer } from 'mobx-react';
import { withTranslation } from 'react-i18next';
import Albumlist from './Albumlist';
import Albumdetails from './Albumdetails';

// const num = 80; //一页加载个数

@withTranslation() @inject('albumAdd') @observer
class Cloudalbum extends React.Component {

  handleOk = () => {
    this.props.albumAdd.handleOk(this.props.handleCancel, this.props.onOk)
  }

  handleCancel = () => {
    this.props.albumAdd.handleCancel(this.props.handleCancel)
  }

  render() {
    const { t, visible } = this.props;
    const {
      ftbtn,
      detail } = this.props.albumAdd;
    return (
      <Modal
        visible={visible}
        title={t('Albumadd')}
        onOk={this.handleOk}
        onCancel={this.handleCancel}
        bodyStyle={{ padding: '0', height: '70vh', position: 'relative' }}
        getContainer={false}
        destroyOnClose={true}
        width='75vw'
        footer={
          ftbtn ? [
            <Button key="submit" type="primary" onClick={this.handleOk}>{t('Determine')}</Button>] : null
        }
      >
        {detail ? <Albumdetails /> : <Albumlist />}
      </Modal >
    );
  }
};
export default Cloudalbum;
