import React, { Component } from 'react';
import { Icon } from 'antd';
import { withTranslation } from 'react-i18next';
import './style.scss'

@withTranslation()
class Information extends Component {
  render() {
    const { t } = this.props;
    return (
      <div className='Information'>
        <div className='Information-iner'>
          <div className='Information-head'>
            <span>{t('detailedinformation')}</span>
            <Icon type="close" onClick={this.props.handlecancel} />
          </div>
          <div className='Information-body'>
            {
              this.props.data.map(item => {
                return (
                  <div className='Information-list' key={item.id}>
                    <div className='p-d-i-icon'><Icon type={item.icon} /></div>
                    <div className='num-text'>
                      <p className='text'>{t(item.txt)}</p>
                      <p className='num'>{item.data}</p>
                    </div>
                  </div>
                )
              })
            }
          </div>
        </div>
      </div>
    );
  }
}

export default Information;