import React from 'react';
import { withTranslation } from 'react-i18next';
import './style.scss';
import { get, observable } from 'mobx';
import { getFile } from '../../util/util';
import { Icon } from 'antd';
import VideoPlayer from '../VideoPlayer';
import { observer } from 'mobx-react';

const videoReg = /(mp4|avi|rmvb|rm|wmv|mov|mpg|mpeg|flv|mkv|3gp|webm|ts|m3u8)$/


@withTranslation() @observer
class BigimgCpt extends React.Component {
  constructor(props) {
    super(props);
    this.inputRef = React.createRef();
    this.cptRef = React.createRef();
    this.imgRef = React.createRef();
  }
  @observable cptModle = false;
  @observable scale = 1;
  @observable x = -50;
  @observable y = -50;
  @observable filg = false;
  isDown = false;
  l = 50;
  r = 50;
  times = 1;


  UNSAFE_componentWillReceiveProps() {
    this.scale = 1;
    this.x = -50
    this.y = -50
    this.l = -this.x;
    this.r = -this.y;
    this.times = 1;
  }

  addcpt = (val) => {
    this.cptHide();
    let cpt = get(this.props.imgfile, 'cpt') || '';
    if (cpt === val) {
      return;
    }
    this.props.blur(val);
  }

  cptShow = () => {
    const { imgfile } = this.props;
    this.cptModle = true;
    setTimeout(() => {
      this.inputRef.current.value = get(imgfile, 'cpt') || '';
    }, 0);
  }

  cptHide = () => {
    this.cptModle = false;
  }

  zoom = (e) => {
    if (!videoReg.test(get(this.props.imgfile, 'ext'))) {
      if (e.deltaY > 0) {
        if (this.scale >= 0.25) {
          this.scale -= 0.05;
        }

      } else {
        if (this.scale <= 4) {
          this.scale += 0.05;
        }
      }
      if (this.scale < 1) {
        this.times = 1 - this.scale + 1;
      } else {
        this.times = 1;
      }
    }
  }

  down = (e) => {
    if (!videoReg.test(get(this.props.imgfile, 'ext'))) {
      e.preventDefault();
      this.box = document.getElementById('imgbox');
      this.body = document.body;
      this.filg = true;
      this.bx = e.clientX;
      this.by = e.clientY;
      this.bw = this.body.offsetWidth / 2;
      this.bh = this.body.offsetHeight / 2;
      window.addEventListener('mousemove', this.move);
    }
  }

  move = (e) => {
    const cliX = e.clientX;
    const cliY = e.clientY;
    this.x = -((((this.bx - cliX) / this.bw * 100) * this.times + this.l));
    this.y = -((((this.by - cliY) / this.bh * 100) * this.times + this.r));
  }

  up = () => {
    window.removeEventListener('mousemove', this.move);
    this.filg = false;
    this.l = -this.x;
    this.r = -this.y;
  }

  render() {
    const { t, closeDialog, imgfile, onPiece, nextPage, blur } = this.props;
    let ViEl;
    if (videoReg.test(get(imgfile, 'ext'))) {
      ViEl = (
        <VideoPlayer
          poster={getFile.file_s(get(imgfile, 'id'))}
          src={getFile.file_b(get(imgfile, 'id'))} />
      )
    } else {
      ViEl = <img src={get(imgfile, 'completed') ? getFile.file_b(imgfile.id) : getFile.file_s(imgfile.id)} alt={get(imgfile, 'id')} />
    }
    return (
      <div className='dialog treeNodeUnselectable'>
        <span className='close-dialog' onClick={closeDialog}><Icon type="close" /></span>
        <div id='imgbody' className='dialog-boby' onWheel={this.zoom} onMouseUp={this.up}>
          <div id='imgbox' className='img-wrap' ref={this.imgRef} onMouseDown={this.down}
            style={{
              transform: `scale(${this.scale}) translate3d(${this.x}%, ${this.y}%, 0px)`
            }}>
            {videoReg.test(get(imgfile, 'ext')) ? null : !get(imgfile, 'completed') && <Icon
              className='icon-load'
              type="loading"
              style={{
                fontSize: 70,
                color: '#fff'
              }} spin />}
            {ViEl}
          </div>
          <div className={`onPiece ${this.filg ? 'hide' : ''}`} onClick={onPiece}>
            <span className='pre-arrow'>
              <Icon type="left" />
            </span>
          </div>
          <div className={`nextPage ${this.filg ? 'hide2' : ''}`} onClick={nextPage}>
            <span className='next-arrow'>
              <Icon type="right" />
            </span>
          </div>
          {blur && <div className={`operation ${this.filg ? 'hide3' : ''}`}>
            <span
              ref={this.cptRef}
              className='ellipsis'
              style={get(imgfile, 'cpt') ? { color: '#FFF' } : { color: '#808080' }}
              onClick={this.cptShow}>
              {get(imgfile, 'cpt') ? get(imgfile, 'cpt') : `${t('adddescription')}...`}
            </span>
          </div>}
        </div>

        {
          this.cptModle && <div className='cpt-modle'>
            <div className='cpt-inner'>
              <div className='cpt-inner-head'>
                <span>{t('AddCpt')}</span>
              </div>
              <div className='cpt-inner-input'>
                <input
                  type="text"
                  ref={this.inputRef}
                  maxLength={100}
                  autoFocus
                  placeholder={t('enterdescription')} />
              </div>
              <div className='cpt-inner-btn'>
                <span className='cancel-btn' onClick={this.cptHide}>{t('Cancel')}</span>
                <span
                  className='confirm-btn'
                  onClick={() => { this.addcpt(this.inputRef.current.value) }}>{t('Determine')}</span>
              </div>
            </div>
          </div>
        }
      </div >
    );
  }
};

export default BigimgCpt;
