import React, { Component } from 'react';
import { Icon, Tooltip } from 'antd';
import { withTranslation } from 'react-i18next';
import { strRep, getFile } from '../../util/util';
import { get } from 'mobx';
import { observer } from 'mobx-react';
import Loading from '../../common/Widget/Loading';
import Nodata from '../../common/Widget/Nodata';

@withTranslation() @observer
class Toframes extends Component {
  render() {
    const { t, framelist, id, framePlay, initload } = this.props;
    return (
      <div className='playlistdetails-main-right'>
        {initload ? <div className='bind-frameslist'>
          {framelist.length > 0 ? framelist.map(item => {
            return (
              <div key={item.node} className='boxlist' style={{ width: item.w, height: item.h}}>
                <div  className='list'>
                  <div className='rahmen-cover'>
                    <div className='cover-img'>
                      <span
                        className='back-img'
                        style={item.profile ? { background: `url("${getFile.file_s(item.profile.id)}") no-repeat`, backgroundSize: '100% 100%' } : {}}></span>
                    </div>
                  </div>
                  <div className='rahmen-name-series'>
                    <p className='name_info ellipsis'>
                      <Tooltip placement="bottom" title={get(item, 'name')} overlayClassName='tooltip'>
                        <span className='name ellipsis'>{item.name}</span>
                      </Tooltip>
                      {item.playlist_id === id && <span className='admin'>{t('default')}</span>}
                    </p>
                    <p className='series'>SN : {strRep(item.serial)} </p>
                  </div>
                  <div className='frame-ringht'>
                    <Tooltip placement="left" overlayClassName='tooltip' title={t('playtoframe')}>
                      <Icon onClick={() => { framePlay([item.node]) }} type="play-square" theme="filled" />
                    </Tooltip>
                  </div>
                </div>
              </div>
            )
          }) : <Nodata />}
        </div> : <Loading />}
      </div>
    );
  }
}

export default Toframes;